






































































// @ is an alias to /src
import Header from '@/components/Header.vue'
import { Component, Vue } from "vue-property-decorator";
import { Toast } from 'vant';

@Component({
  components: {
    Header
  }
})
export default class Deposit extends Vue {
  private currBankIndex: number = 0;
  private showBankPopup?: Boolean = false;
  private pageTitle?: String = '存款'
  private bankName?: String = '工商银行'
  private amount?: any = ''
  private bankDetails?: any = [
    { img: '', label: '网银支付' },
    { img: '', label: '银行卡转账' },
    { img: 'Alipay', label: '支付宝' },
    { img: 'UnionPay', label: '银联扫码' },
    { img: 'WechatH5', label: '微信H5' },
    { img: 'Wechat', label: '微信支付' },
    { img: 'QQWalletH5', label: 'QQ钱包H5' },
    { img: 'QuickPay', label: '快捷支付' },
    { img: 'AlipayBarCode', label: '支付宝扫码' },
    { img: 'AlipayH5', label: '支付宝H5' },
  ]
  private depositAmounts?: any = [
    '107',
    '502',
    '1008',
    '2001',
    '4999'
  ]
  private columns?: any = [
    '建设银行', 
    '招商银行', 
    '工商银行', 
    '农业银行', 
    '中国银行'
  ]

  private selectBank(index: number) {
    this.currBankIndex = index;
  }

  private selectAmount(val: any) {
    this.amount = val;
  }
  private onChange(picker:any, value:any, index:any) {
    console.log(`Value: ${value}, Index: ${index}`)
    this.bankName = value
    this.showBankPopup = false
  }

  private onSelect(item:any) {
    console.log(item)
    this.showBankPopup = false;
    Toast(item.name);
  }
}
